<template>
    <div id="theme">
        <b-nav-item-dropdown text="" right>
            <b-dropdown-item href="#" @click="dayOrNight(1)"></b-dropdown-item>
            <b-dropdown-item href="#" @click="dayOrNight(0)"></b-dropdown-item>
            <b-dropdown-item href="#" @click="dayOrNight(2,0)">-</b-dropdown-item>
            <!--  -->
            <b-modal v-model="diyColor" title="" :header-class="'body-class'" dialog-class="dialog-class"
            :body-class="'body-class'" modal-class="modal-class" hide-footer>
            <form ref="form" @submit.stop.prevent="handleSubmit" class="dilog-input">
                <b-card>
                    <div class="chooseColor" ref="colorCase">
                        <div class="color1">
                            <span>1</span>
                            <label @click="setGrad(0)"></label>
                        </div>
                        <div class="color1">
                            <span>2</span>
                            <label @click="setGrad(1)"></label>
                        </div>
                        <div class="gradient">
                            <span></span>
                            <b-form-checkbox switch size="lg" v-model="gradType"></b-form-checkbox>
                            <span></span>
                        </div>
                        <b-form-select v-model="chooseOpt" :options="options" @change="gradOptVal"></b-form-select>
                    </div>
                    <colorPicker :value="color" @input="updateValue"></colorPicker>
                </b-card>
                <b-button class="mt-3" variant="outline-primary" block @click="diyColor=false"> </b-button>
            </form>
            </b-modal>
            <b-dropdown-item href="#" @click="dayOrNight(3,0)">-</b-dropdown-item>
            <b-modal v-model="diyBg" title="" :header-class="'body-class'" dialog-class="dialog-class"
            :body-class="'body-class'" modal-class="modal-class" hide-footer>
            <form ref="form" @submit.stop.prevent="handleSubmit" class="dilog-input">
                <b-form-file multiple :file-name-formatter="formatNames" :placeholder="tip" browse-text="" autofocus @change="choosePic" accept="image/*"></b-form-file>
                <b-button class="mt-3" variant="outline-primary" block @click="diyBg=false"> </b-button>
            </form>
            </b-modal>
        </b-nav-item-dropdown>
    </div>
</template>

<script>
// 
import theme from '@/assets/js/theme'
import {Sketch  as colorPicker} from 'vue-color'
let themeEle = document.getElementById('style')
let mainArea=document.getElementsByClassName('content-theme')[0]
    export default {
        data(){
            return{
                diyColor:false, //
                diyBg:false,     //
                tip:'',
                color: 'rgba(11,62,81,0.66)', // rgba(11,62,81,0.66)
                gradType:true, //-:true :false
                chooseOpt:null, //
                options: [  //
                    { value: null, text: '',disabled: true },
                    { value: 'ellipse', text: '' },
                    { value: 'circle', text: '' }
                ],
                oldOpt:null, //optios
                selectOpt:'ellipse', //-select
                color1Flag:true,color2Flag:false, //12
                color1:'rgba(11,62,81,0.66)',color2:'rgba(11,62,81,0.66)',
                gradOptions:{}, //
                // indexedDB-(base64)
                myDB:{
                    name: 'myDB',
                    version: 1.0,
                    db: null
                },
                bgImg:{
                    name:'bgImg',
                    id:1,
                    value:''
                }
            }
        },
        methods: {
            //  -  falsetrue
            dayOrNight(flag,judeg) {
                flag=Number(flag)
                switch (flag) {
                    case 0: { // 
                        themeEle.innerHTML = theme.day
                    };
                    break;
                    case 1: { // 
                        themeEle.innerHTML = null
                    };
                    break;
                    case 2:{ // 
                        if(judeg && this.$store.state.gradOptions && this.$store.state.theme==flag){
                            this.setGrad(3) //
                        }else{ //
                            this.diyColor=true
                            // 
                            if(this.$store.state.gradOptions){
                                this.setGrad(3)
                            }else{
                                this.setGrad(2)
                            }
                            
                        }
                    };break;
                    case 3:{ // 
                        if(judeg &&  this.$store.state.theme==flag){
                            this.choosePic()
                        }else{
                            // if(!this.$store.state.diyBg) this.tip=''
                            this.diyBg=true
                            this.choosePic()
                            this.$store.commit('alert','')
                        }
                        
                    };break;
                }
                this.$store.commit('changeTheme', flag)
            },
            // 
            formatNames(files) {
                if (files.length === 1) {
                    return files[0].name
                } else {
                    return `${files.length} `
                }
            },
            // 
            choosePic(files){
                let oldSty=themeEle.innerHTML.toString().split('.content-theme')[0] //
                if(files){
                    this.setBase64(files.target.files[0])
                }else{
                    this.getData(1)
                }
                this.$store.commit('diyBg',1)
            },
            // indexedDBbase64
            setBase64(file) {
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = ()=>{
                    //base64returnundefined
                    let base64=reader.result
                    let obj={
                        id:1,
                        value:reader.result
                    }
                    this.putData(obj)
                }
            },
            // 
            updateValue (val) {
                this.color = `rgba(${val.rgba.r},${val.rgba.g},${val.rgba.b},${val.rgba.a.toFixed(2)})`
                this.setGrad()
            },
            // -select
            gradOptVal(val){
                this.selectOpt=val
            },
            // 
            setGrad(judeg=-1){
                let options=this.$store.state.gradOptions
                if(options){ //
                    options=JSON.parse(options.toString())
                }
                switch(Number(judeg)){
                    case 0:{ //1
                        this.color1Flag=true
                        this.color2Flag=false
                        options.color1=this.color
                    };break;
                    case 1:{ //2
                        this.color1Flag=false
                        this.color2Flag=true
                        options.color2=this.color
                    };break;
                    case 2:{ //
                        options={
                            'color1':this.color1,
                            'color2':this.color2,
                            'type':this.gradType,
                            'select':this.selectOpt
                        }
                    };break;
                    case 3:{ //
                        options
                    };break;
                    default:{
                        // -
                        if(this.color1Flag){ //1
                            options.color1=this.color
                        }
                        if(this.color2Flag){ //2
                            options.color2=this.color
                        }
                    }
                }
                // 
                this.$nextTick(()=>{
                    this.setColorCase(options)
                })
                
            },
            // 
            setColorCase(options){
                // 
                let colorCase=this.$refs.colorCase
                if(colorCase) {
                    colorCase=Array.from(colorCase.childNodes) //
                    colorCase.some((item,i)=>{
                        if(i<2){
                            item=item.children[1]
                            if(i==0){ //color1
                                item.style= `background:${options.color1}`
                                this.color1=options.color1
                                return
                            } 
                            if(i==1){ //color2
                                item.style= `background:${options.color2}`
                                this.color2=options.color2
                                return
                            }
                        }
                    })
                }
                let sty=theme.diyColor(options.type,options.select,options.color1,options.color2)
                this.setBgStyle(sty)
                this.gradOptions=options
                // 
                this.$store.commit('gradOptions',JSON.stringify(options))
            },
            
            // Ele
            setBgStyle(style){
                let oldSty=themeEle.innerHTML.toString().split('.content-theme')[0] //
                themeEle.innerHTML=`${oldSty.trim()}${style}`
            },
            // indexedDB
            indexedDB(){
                let indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB || window.msIndexedDB
                if(!indexedDB){
                    this.$store.commit('alert',"IndexedDB")
                    return false
                }
                let _this=this
                // 
                var request = indexedDB.open(_this.myDB.name, _this.myDB.version)
                request.onerror = function (e) { //
                    _this.$store.commit('alert','')
                }
                request.onsuccess = function (e) { //
                    _this.myDB.db = e.target.result
                    if(_this.$store.state.theme==3) _this.getData(1) //indexedDB
                }
                request.onupgradeneeded = function (e) { //
                    var db = e.target.result;
                    if (!db.objectStoreNames.contains(_this.bgImg.name)) { //
                        var store = db.createObjectStore(_this.bgImg.name, { keyPath: 'id',autoIncrement: true});
                        store.createIndex('value', 'value', { unique: true })
                        store.transaction.oncomplete=()=>{ //
                            var transaction = db.transaction(_this.bgImg.name, 'readwrite').objectStore(_this.bgImg.name)
                            transaction.add(_this.bgImg)
                        }
                    }
                }
            },
            // indexedDB
            getData(id){
                let transaction=this.myDB.db.transaction(this.bgImg.name,'readwrite').objectStore(this.bgImg.name)
                let request=transaction.get(id) //
                request.onsuccess=(e)=>{
                    let data=e.target.result
                    this.setBgStyle(theme.diyBg(data.value)) //
                }
                request.onerror=(err)=>{
                    this.$store.commit('alert','')
                }
            },
            // indexedDB
            putData(obj){
                if(JSON.stringify(obj)===undefined) obj=this.bgImg
                let transaction=this.myDB.db.transaction(this.bgImg.name,'readwrite').objectStore(this.bgImg.name)
                let request=transaction.get(obj.id)
                request.onsuccess=(e)=>{
                    let data=e.target.result
                    data.value=obj.value
                    let update=transaction.put(data)
                    update.onsuccess=()=>{
                        this.getData(1)
                    }
                }
                request.onerror=(err)=>{
                    this.$store.commit('alert','')
                }
            }
        },
        mounted() {
            // 
            this.indexedDB()
            // 
            this.dayOrNight(this.$store.state.theme,1)
        },
        watch:{
            // 
            '$store.state.theme':{
                handler(val){
                    this.dayOrNight(this.$store.state.theme,1)
                },
            },
            // -truefalse
            gradType:{
                handler(val){
                    if(!this.gradType){
                        this.oldOpt=this.options
                        this.selectOpt='to bottom'
                        this.options=[
                            { value: null, text: '',disabled: true },
                            { value: 'to bottom', text: ''},
                            { value: 'to top', text: ''},
                            { value: 'to right', text: ''},
                            { value: 'to left', text: ''},
                            { value: 'to bottom right', text: ''},
                            { value: 'to top right', text: ''},
                            { value: 'to bottom left', text: ''},
                            { value: 'to top left', text: ''},
                        ]
                    }else{
                        this.options=this.oldOpt
                        this.selectOpt='ellipse'
                    }
                    this.gradOptions.type=this.gradType
                    this.setColorCase(this.gradOptions)
                }
            },
            // -select
            selectOpt:{
                handler(){
                    this.gradOptions.select=this.selectOpt
                    this.setColorCase(this.gradOptions)
                }
            },
        },
        components:{
            colorPicker
        }
    }
</script>

<style lang="less" scoped>
.chooseColor{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 110px;
    padding-bottom: 10px;
    .color1,.color2{
        display: flex;
        justify-content: space-around;
        align-items: center;
        label{
            display: block;
            width: 20px;
            height: 20px;
            margin: 0 0 0 5px;
            background: chocolate;
            border: 1px solid chocolate;
        }
    }
    .gradient{
        display: flex;
        justify-content: space-around;
        align-items: center;
        max-width: 200px;
        width: 100%;
    }
}
.card{
    border: 1px solid #007ACC;
}

</style>